.no_results {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background: black;
    color: white;
    margin: 20px 0 10px 0;
  }
  
  .no_results p {
    transform: translateY(50px);
    font-size: 1rem;
  }

  
.no_results img {
  width: 500px;
}

  
  .no_results img {
    width: 500px;
  }
  
  @media (max-width: 700px) {
    .no_results img {
      width: 500px;
    }
  
    .no_results p {
      font-size: 0.8rem;
    }
  }
  
  @media (max-width: 500px) {
    .no_results img {
      width: 280px;
    }
    .no_results p {
      transform: translateY(30px);
      font-size: 0.6rem;
    }
  }