.home_card * {
    transition: 0.2s ease;
  }
  
  .home_card {
    margin: 20px;
    width: 300px;
    height: 300px;
  }
  
  .home_card br {
    margin: 0;
  }
  
  .card_ext_bg {
    position: absolute;
    margin: 0;
    width: inherit;
    height: inherit;
    background-color: rgba(0, 0, 0, 0.7);
    opacity: 0;
    z-index: 1;
  }
  
  .card_content {
    margin: 20px;
  }
  
  .home_card:hover .card_ext_bg {
    opacity: 1;
  }
  
  .home_card:hover .card_content {
    position: absolute;
    z-index: 2;
    padding-right: 20px;
    width: 260px;
  }
  
  @media (max-width: 320px) {
    .home_card {
      margin: 10px;
      width: 250px;
      height: 300px;
    }
  }