.shopping_card {
    overflow-y: scroll;
    z-index: 10;
    width: calc(30vw + 30px);
    height: 80vh;
  }
  
  .shopping_card img {
    width: 30vw;
  }
  
  @media (max-width: 650px) {
    .shopping_card {
      width: calc(80vw + 30px);
    }
    .shopping_card img {
      width: 80vw;
    }
  }
  
  .shopping_card::-webkit-scrollbar {
    height: 0;
    display: none;
  }
  
  .emt_crd {
    height: 50px;
    width: 100px;
  }
  
  .shopping_card a {
    letter-spacing: 1px;
    cursor: pointer;
    padding: 10px 20px;
    position: absolute;
    margin: 10px;
    right: 10px;
    background: black;
    color: rgba(255, 255, 255, 0.7);
    border-radius: 30px;
    border: none;
    transition: 0.2s ease;
    text-decoration: none;
  }
  
  .shopping_card a:hover {
    background: red;
    color: white;
  }
  
  .close_btn {
    position: absolute;
    right: 0;
    top: 0;
    padding: 5px;
    background: red;
    height: 40px;
    width: 40px;
    z-index: 50;
    transition: 0.2s ease;
  }
  
  .close_btn:hover {
    color: rgba(255, 255, 255, 0.9);
  }